<template>
    <v-container class="container">
      <v-card class="ma-auto mt-16 pa-10" width="40vw">
        <v-card-title class="text-h5 justify-center">Registrarme</v-card-title>
        <v-divider></v-divider>
        <v-form v-model="formValue">
          <v-text-field
            label="email"
            v-model="email"
            :rules="emailRules"
          ></v-text-field>
          <v-text-field
            label="username"
            v-model="username"
            :rules="inputRules"
          ></v-text-field>
          <v-text-field
            label="contraseña"
            v-model="password"
            :rules="inputRules"
            type="password"
          ></v-text-field>
          <v-text-field
            label="contraseña"
            v-model="passwordCompare"
            :rules="passwordRules"
            type="password"
          ></v-text-field>
          <v-card-actions>
            <v-btn block color="primary" :loading="registerLoad" @click="register">Registrarme</v-btn>
          </v-card-actions>
          <v-divider></v-divider>
          <v-card-text class="text-h6 text-center">¿Ya tienes cuenta?</v-card-text>
          <v-card-actions>
            <v-btn to="/login" block color="secondary">Iniciar Sesión</v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-container>
  </template>
  
  <script>
  import validator from 'validator'

  export default {
    data(){
      return{
        registerLoad: false,
        formValue: false,
        username: '',
        password: '',
        passwordCompare: '',
        inputRules: [value => !!value || 'Este campo no puede estar vacío.'],
        emailRules: [
            value => !!value || 'Este campo no puede estar vacío.',
            value => validator.isEmail(value) || 'Debe contener un email valido.'
        ]
      }
    },
    computed:{
        passwordRules(){
            return [
                value => this.password === value || 'Las contraseñas deben ser iguales.'
            ]
        }
    },
    methods:{
      register(){
        if(this.formValue) this.registerLoad = true
      }
    }
  }
  </script>
  
  <style scoped>
    .container{
      height: 100vh;
    }
  </style>